.table{
    .cellWrapper{
        display: flex;
        align-items: center;

        .image{
            width: 32px;
            height: 32px;
            border-radius: 50%;
            margin-right:10px;
            object-fit: cover;
        }
    }

    .status{
        padding: 5px;
        border-radius: 5px;

        &.IN {
            color: green;
            background-color: rgba(157, 157, 28, 0.103);
            
        }
        &.OUT {
            color: goldenrod;
            background-color: rgba(21, 143, 21, 0.151);
        }
    }
}